<template>
  <div class="group-box">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>单词本管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div>
      <h1>单词本管理</h1>
      <p class="not-importent">
        你可以在此处设置首页的推荐词条范围，若选择关闭部分单词本，此单词本下的词条将不再出现在首页上。
      </p>
    </div>
    <div class="groups">
      <div class="group flex-between" v-for="group in groups" :key="group._id">
        <div>{{ group.name }}</div>
        <div>
          <el-switch
            v-model="group.selected"
            active-color="#13ce66"
            inactive-color="#ddd"
            @change="selectedGroupsChange"
          >
          </el-switch>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CloudUtils from "./CloudUtils";
export default {
  name: "GroupBox",
  data() {
    return {
      groups: [],
    };
  },
  created() {
    CloudUtils.getAll(
      this.cloud.database().collection("mpa_content_group")
    ).then((groups) => {
      const selectedGroups = JSON.parse(
        localStorage.getItem("selectedGroups") || "[]"
      );
      const groupIds = selectedGroups.map((group) => {
        return group._id;
      });
      this.groups = groups.map((group) => {
        group.selected = groupIds.includes(group._id);
        return group;
      });
    });
  },
  methods: {
    selectedGroupsChange() {
      const selectedGroups = this.groups.filter((group) => {
        return group.selected;
      });
      // 放置到本地存储中
      localStorage.setItem("selectedGroups", JSON.stringify(selectedGroups));
      localStorage.setItem("selectedGroupsChanged", Date.now())
    },
  },
};
</script>

<style scoped>
.group-box {
  padding: 15px;
}
.groups {
  margin-top: 40px;
}
.group {
  margin: 15px 0;
}
</style>