<template>
  <div class="index" v-if="currentMpaContent">
    <div class="content" style="padding: 15px">
      <h1>{{ showContent }}</h1>
      <p v-for="remark in currentMpaContent.remarks" :key="remark">
        {{ remark }}
      </p>
    </div>
    <div class="operation">
      <div class="flex-between" style="margin: 15px; align-items: flex-end">
        <div style="flex: auto; margin-right: 20px">
          <div @click="$router.push('/group-box')">
            <el-link class="group" style="color: #eee"
              ><i class="el-icon-s-operation"></i
              >{{ groupMap[currentMpaContent.group].name }}</el-link
            >
          </div>
          <div class="flex-start content-words">
            <div
              v-for="contentWord in contentWords"
              :key="contentWord.index"
              class="content-word"
            >
              <el-button circle @click="answer = answer + contentWord.word">{{
                contentWord.word
              }}</el-button>
            </div>
          </div>
        </div>
        <div class="flex-start operation-menus">
          <div class="operation-menu">
            <el-button
              type="warning"
              icon="el-icon-back"
              circle
              @click="answer = answer.substr(0, answer.length - 1)"
              :disabled="!answer"
            >
            </el-button>
          </div>
          <div class="operation-menu">
            <el-button
              :type="
                answer == currentMpaContent.content ? 'primary' : 'default'
              "
              icon="el-icon-bottom"
              circle
              @click="nextMpaContent"
            ></el-button>
          </div>
        </div>
      </div>
    </div>
    <div class="bg" :style="{'min-height':minHeight + 'px'}">
      <img
        :src="image"
        alt="奖励"
        width="100%"
        v-if="image"
        v-show="answerFinishedRate"
        :style="{ opacity: answerFinishedRate }"
      />
    </div>
  </div>
</template>

<script>
import CloudUtils from "./CloudUtils";
import ImageUtils from "./ImageUtils";
export default {
  name: "Index",
  data() {
    return {
      mpaContents: [],
      currentMpaContentIndex: -1,
      groupMap: {},
      selectedGroups: [],
      answer: "",
      image: "",
      minHeight: window.innerHeight
    };
  },
  created() {
    this.loadGroupMap().then(() => {
      this.loadSelectedGroups().then(() => {
        this.loadMpaContents().then(() => {
          this.nextMpaContent();
        });
      });
    });
  },
  activated() {
    if (localStorage.getItem("selectedGroupsChanged")) {
      localStorage.removeItem("selectedGroupsChanged");
      this.loadSelectedGroups().then(() => {
        this.loadMpaContents(true).then(() => {
          this.nextMpaContent();
        });
      });
    }
  },
  methods: {
    nextMpaContent() {
      this.updateIndex(this.currentMpaContentIndex + 1);
    },
    updateIndex(index) {
      this.currentMpaContentIndex = index;
      this.answer = "";
      ImageUtils.randomOne().then((image) => {
        this.image = image;
      });
      // 快到头了，继续加载一批
      if (this.currentMpaContentIndex > this.mpaContents.length - 3) {
        this.loadMpaContents();
      }
    },
    loadSelectedGroups() {
      // 先从本地缓存加载
      const selectedGroups = JSON.parse(
        localStorage.getItem("selectedGroups") || "[]"
      );
      if (selectedGroups.length) {
        this.selectedGroups = selectedGroups;
        return Promise.resolve();
      }
      return this.cloud
        .database()
        .collection("mpa_content_group")
        .aggregate()
        .sample({
          size: 3,
        })
        .end()
        .then((res) => {
          localStorage.setItem("selectedGroups", JSON.stringify(res.data));
          this.selectedGroups = res.data;
        });
    },
    loadMpaContents(unconcat) {
      return this.cloud
        .callFunction({
          name: "loadMpaContents",
          data: {
            groups: this.selectedGroups.map((group) => {
              return group._id;
            }),
          },
        })
        .then((res) => {
          this.mpaContents = unconcat
            ? res.result
            : this.mpaContents.concat(res.result);
          this.currentMpaContentIndex = unconcat
            ? -1
            : this.currentMpaContentIndex;
        })
        .catch((err) => {
          console.error(err);
          this.$message.error("出错了");
        });
    },
    loadGroupMap() {
      return CloudUtils.getAll(
        this.cloud.database().collection("mpa_content_group")
      ).then((groups) => {
        this.groupMap = groups.reduce((map, group) => {
          map[group._id] = group;
          return map;
        }, {});
      });
    },
  },
  computed: {
    currentMpaContent() {
      return this.mpaContents[this.currentMpaContentIndex];
    },
    contentWords() {
      const currentMpaContent = this.mpaContents[this.currentMpaContentIndex];
      if (currentMpaContent) {
        const list = currentMpaContent.content.split("").map((word, index) => {
          return {
            word,
            index,
          };
        });
        for (let i = 0; i < list.length; i++) {
          const temp = list[i];
          const dest = Math.floor(Math.random() * list.length);
          list[i] = list[dest];
          list[dest] = temp;
        }
        return list;
      }
      return [];
    },
    showContent() {
      const currentMpaContent = this.mpaContents[this.currentMpaContentIndex];
      if (currentMpaContent) {
        var result = [];
        for (var i = 0; i < currentMpaContent.content.length; i++) {
          result.push(this.answer[i] || "*");
        }
        return result.join("");
      }
      return "";
    },
    answerFinishedRate() {
      const currentMpaContent = this.mpaContents[this.currentMpaContentIndex];
      if (currentMpaContent) {
        const content = currentMpaContent.content;
        if (content.indexOf(this.answer) == 0) {
          return this.answer.length / content.length;
        }
        return 0;
      }
      return 0;
    },
  },
};
</script>

